import React, { Component } from 'react';
import request from 'superagent';
import BookList from './BookList';

class Books extends Component {
    state = {
        books: [],
        searchBook: '',
        sort: ''
    }

    searchBook = (e) => {
        e.preventDefault();
        request
            .get("https://www.googleapis.com/books/v1/volumes?maxResults=39")
            .query({ q: this.state.searchField })
            .then((data) => {
                const cleanData = this.cleanData(data);
                this.setState( { books: cleanData  } )
            })
    }

    handleSearch = (e) => { // submit event
        this.setState( { searchField: e.target.value })
    }

    handleSort = (e) => { // selected event
        this.setState( { sort: e.target.value })
    }
    // 필터링 년도 정보 없으면 0000, 이미지 정보 없으면 ? 이미지로 대체.
    cleanData = (data) => {
        const cleanedData = data.body.items.map((book) => {
            if(book.volumeInfo.hasOwnProperty('publishedDate') === false ) {
                book.volumeInfo['publishedDate'] = '0000';
            }else if(book.volumeInfo.hasOwnProperty('imageLinks') === false) {
                book.volumeInfo['imageLinks'] = { thumbnail: 'https://upload.wikimedia.org/wikipedia/commons/thumb/4/46/Question_mark_%28black%29.svg/1200px-Question_mark_%28black%29.svg.png' }
            }
            return book;
        })
        return cleanedData;
    }

    render() {
        const sortedBooks = this.state.books.sort((a, b) => {
            if(this.state.sort === 'Newest') {
                return parseInt(b.volumeInfo.publishedDate.substring(0, 4)) - parseInt(a.volumeInfo.publishedDate.substring(0, 4))
            }
            else if(this.state.sort === 'Oldest') {
                return parseInt(a.volumeInfo.publishedDate.substring(0, 4)) - parseInt(b.volumeInfo.publishedDate.substring(0, 4))
            }
        })

        return (     
            <div className = 'container'>
                <div className = "search-area">
                    <form onSubmit = { this.searchBook } action = "">
                        <input onChange = { this.handleSearch } type = "text" placeholder = "SEARCH"/>
                        <button type = "submit">Search</button>
                        <select defaultValue = "Sort" onChange = { this.handleSort }>
                            <option disabled value ="Sort">Sort</option>
                            <option value ="Newest">Newest</option>
                            <option value ="Oldest">Oldest</option>
                        </select>
                    </form>
                </div>
                <BookList books = { sortedBooks } />
            </div>
        );
    }
}

export default Books;